@import url(https://fonts.googleapis.com/css2?family=Freckle+Face&family=Roboto+Condensed:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: rgb(2,0,36);
  background: linear-gradient(180deg, rgba(2,0,36,1) 0%, rgba(34,66,65,1) 50%, rgba(12,202,196,1) 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card-container {
    display: flex;
    flex-direction: column;
    background-color: #95dada;
    border: 1px solid grey;
    border-radius: 5px;
    padding: 25px;
    cursor: pointer;
    -moz-osx-font-smoothing: grayscale;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    transform: translateZ(0);
    transition: transform 0.25s ease-out;
  }
  
  .card-container:hover {
    transform: scale(1.05);
  }
  
  .card-img {
    display: block;
    max-width: 100%;
  }
.card-list {
    width: 85vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
    grid-gap: 20px;
}

.search {
    -webkit-appearance: none;
    border: none;
    border-radius: 5px;
    outline: none;
    padding: 10px;
    width: 150px;
    line-height: 30px;
    margin-bottom: 30px;
  }
  
.footer-fineprint {
    font-size: .85rem;
    margin-top: 5em;
    opacity: .3;
}
*,
*::before,
*::after {
    box-sizing: border-box;
}

.App {
  text-align: center;
}

h1 {
  font-family: 'Freckle Face', cursive;
  font-size: 72px;
  color: #0ccac4;
}

h2, p {
  font-family: 'Roboto Condensed', sans-serif;
}


